'use client';

import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export default function ProductosPage() {
  return (
    <div className="space-y-6">
      <header>
        <h1 className="text-3xl font-bold mb-2 text-secondary-900">Productos</h1>
        <p className="text-secondary-600 mb-6">
          Gestión de productos e inventario del punto de venta.
        </p>
      </header>

      <Card>
        <CardHeader>
          <CardTitle>Listado de productos</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-secondary-500">
            Aquí irá la tabla de productos. Por ahora, el módulo de productos comparte el mismo estilo
            de tarjetas y layout que el resto del dashboard para una experiencia más moderna y consistente.
          </p>
        </CardContent>
      </Card>
    </div>
  );
}
